  content: "";
  display: block;
  padding-top: var(--aspect-ratio);
}
.ratio__content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.ratio--4x3 {
  --aspect-ratio: 75%;
}

.ratio--16x9 {
  --aspect-ratio: 56.25%;
}

.ratio--21x9 {
  --aspect-ratio: 42.8571428571%;
}

