/*
	INFO: Для экономии ресурсов и уменьшения времени сборки рекомендуется использовать шрифты только в формате woff2.
	Подключение шрифта в других форматах не требуется просто потому, что в PDF нет необходимости в обеспечении
	кроссбраузерности шрифтов.
*/


